/* GENERAL */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: 'Helvetica Neue', sans-serif, 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:white;
}

#root {
  width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: black;
}

li {
  list-style: none;
}

a:hover {
  font-weight: bold;
}

/* HEADER */
header {
  position: sticky;
  top: 0;
  font-size: 1.5rem;
  z-index: 10;
}

#logo {
  display: inline-block;
  width: 100%;
  height: 100px;
  padding: 5px;
}

nav {
  opacity: 50%;
}

#navButtons {
  position: absolute;
  left: -5%;
  top: 0.8rem;
  display: flex;
  gap: 1rem;
  flex-direction: column;
}

#navButtons button, #rentBtn, #postBtn, #deleteBtn, .addItemForm button {
  border-radius: 10px;
  font-weight: bold;
  padding:10px;
  min-height: 2rem; 
  min-width: 2rem;
  border: 0;
  box-shadow: 3px 0 2px 1px black;
}

#navButtons button:hover, #rentBtn:hover {
  box-shadow: 9px 0 4px 2px black;
}

/* SEARCH */
#searchForm {
  margin: 1rem auto;
  min-width: 20rem;
  max-width: 40rem;
  box-shadow: none;
}

#searchForm label {
  position: absolute;
  left: -10000px;
}

#searchForm input {
  display: block;
  width: 100%;
  height: 2rem;
  border-radius: 20px;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 10px;
}

/* HOME */
.homepage {
  padding: 0 1rem;
  position: relative;
}

.items {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-flow: row wrap;
  padding-bottom: 50px;
  z-index: -1;
}

.item {
  display: flex;
  gap: 0.2rem;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

#itemImage {
  display: block;
  width: 90%;
  max-width: 15rem;
  margin: 0 auto;
  border-radius: 15px;
} 

/* DASHBOARD */
.userProfile {
  padding: 2rem;
}
.userInfo {
  display: flex;
  gap: 1rem;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.userInfo h4 {
  color:rgb(171, 35, 150);
  margin-bottom: 1rem;
  font-size: large;
}

#avatar {
  max-width: 15rem;
  border-radius: 15px;
}

#userItems {
  display: flex;
  gap: 1rem;
  flex-direction: row;
  flex-flow: row wrap;
  justify-content: center;
}

/* ITEM DETAILS */
.itemDetails {
  padding: 2rem;
  display: flex;
  gap: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.itemDetails h3 {
  text-align: center;
}

.itemDetails p {
  margin: 0.8rem auto;
  width: 50%;
  text-align: left;
  border-top: 1px dashed black;
  padding-top: 0.8rem;
}

#rentBtn, #deleteBtn, #postBtn, .addItemForm button {
  display: block;
  margin: 1rem auto;
  min-width: 10rem;
  max-width: 25rem;
  background-color: rgb(223, 193, 198);
}

#deleteBtn {
  background-color: black;
  color: white;
}

#reserveDate {
  display: block;
  margin: 1rem auto;
  width: 50%;
  border-radius: 10px;
  min-height: 1rem;
  padding: 1rem;
}

.btnContainer {
  position: relative;
}

#reservationSuccess {
  position: relative;
  background-color: rgba(231, 240, 99, 0.3);
  border-radius: 10px;
  top: 0;
  padding: 1rem;
}

/* ADD ITEM FORM */
.addItemForm {
  border: 1px solid black;
  border-radius: 15px;
  box-shadow: 0 4px 4px rgba(0,0,0,0.2);
  max-width: 25rem;
  min-width: 25rem;
  margin: 2rem auto;
  padding: 2rem;
  display: flex;
  gap: 0.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.addItemForm input {
  border-radius: 10px;
  box-shadow: 0 4px 4px rgba(0,0,0,0.2);
  min-height: 1rem;
  max-width: 95%;
  padding: 1rem;
}

/* FOOTER */
.footer {
  background-color: rgba(60, 213, 218, 0.3);
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  text-align: center;
}

@media screen and (max-width: 480px) {
  #itemImage {
    width: 100%;
  }
}

@media screen and (max-width: 800px) {
  .addItemForm {
    width: 90%;
  }
}